// @use "../Helpers/variales" as *;

.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  @media (min-width: 768px) {
    width: 750px;
  }
  @media (min-width: 992px) {
    width: 970px;
  }
  @media (min-width: 1200px) {
    width: 1170px;
  }
}
.main {
  position: relative;
  min-height: calc(100vh - 80px);
  top: 80px;
  overflow: hidden;
}
